const Content = ({ data }) =>{
  if(data == null){
    return (
      <div className = "content">
        <p>loading...</p>
      </div>
    );
  }
  return (
    <div className = "columns is-vsentered is-multiline">
      {data.message.map((url, i) => {
        return (
          <div key = {i} className = "column is-3">
            <Image url = {url} />
          </div>
        )
      })}
    </div>
  )
}

const App = () => {
  const [data, setData] = useState(null);
  useEffect(() => {
    fetch("https://dog.ceo/api/breeds/image/random/100")
    .then((response) => response.json())
    .then((data) => {
      setData(data)
    });
  }, []);
  return (
    <div>
      <Hero />
      <Contents />
      <Body1 />
      <Body2 />
      <Body3 />
      <Body4 />
      <section className = "section">
        <div className = "container">
          <Content data = {data} />
        </div>
      </section>
      <Footer />
    </div>
  );
};

export default App;